$(window).on('load',function() {
	var all_skills = [
		'University College London',
		'Imperial College',
		'Kings College London',
		'London School of Economics',
		'Manchester University',
		'University of Bath'
	];
    $('#university').autocomplete({
        source: all_skills,
        minLength: 4,
        autoFocus: true,
    	delay: 250
    });

});
